import React from "react";
import Questions from "../Questions";
import { useSelector } from "react-redux";

const QuestionCarausel = (props) => {

  const currentQuestion = useSelector((state) => state.qChanger.currentQNo);

  return (
    <div className='relative px-12 py-12 pb-5 overflow-y-scroll h-5/6'>
      <Questions
        id={currentQuestion}
        cAnswerArray={props.questionList[currentQuestion].CorrectAnswer}
        style={{ "margin-top": "20px" }}
        options={props.questionList[currentQuestion].OptionList}
        questionNumber={currentQuestion + 1}
        corrMarks={props.questionList[currentQuestion].CorrectMarks}
        negMarks={props.questionList[currentQuestion].NegativeMarks}
        Qtext={props.questionList[currentQuestion].Question}
      />
    </div>
  );
};

export default QuestionCarausel;
