import { createSlice } from '@reduxjs/toolkit';

const initialArray = {answerArray: [],answered:[],marked:[]}

export const answerSlice = createSlice({
    name: 'answer',
    initialState: initialArray,
    reducers: {
        add(state,action){
            state.answerArray[action.payload.index] = action.payload.answer;
            state.answered[action.payload.index] = action.payload.answer;
        },
        mark(state,action){
            state.marked[action.payload] = true;
        },
        setInitial(state,action){
            state.answerArray = action.payload;
        }
    }
})