import React, { Fragment, useEffect, useState } from "react";
import Button from "../UI/Button";
import { useSelector, useDispatch } from "react-redux";
import { paperActions, answerActions, qChangerActions } from "../../Store";
import { useRef } from "react";
import { json, Navigate } from "react-router-dom";
import WaitingCounter from "./WaitingCounter";

const Waiting = () => {
  const examCodeRef = useRef();
  const dispatch = useDispatch();
  const person = useSelector((state) => state.auth.id);
  const id = sessionStorage.getItem('id')
  const personId = person || Number(JSON.parse(id));
  const getPaper = () => {
    fetchPaper();
  };
  const fetchPaper = async () => {
    const Response = await fetch("/entercode", {
      method: "POST",
      body: JSON.stringify({
        examCode: examCodeRef.current.value,
        enrollment_number: personId,
      }),
      headers: { "Content-Type": "application/json" },
    });
    if (Response.ok) {
      const data = await Response.json();
      dispatch(paperActions.eligibility(data.eligibility))
      if (data.eligibility) {
        dispatch(paperActions.setId(data.questionpaper.examid));
        dispatch(paperActions.fetchPaper(data.questionpaper.questionList));
        dispatch(paperActions.duration(data.duration));
        dispatch(paperActions.remainTime(data.remainingTime));
        if (data.difference === "Negative") {
          if (data.remainingTime >= data.duration) {
            dispatch(paperActions.missed());
            dispatch(paperActions.endExam());
          }
          dispatch(paperActions.duration(data.duration + data.remainingTime));
        }
        setCode(true);
      }
    }
  };
  const [code, setCode] = useState(false);
  const [completed, setCompleted] = useState(false);
  const examid = useSelector((state) => state.paper.examid);
  const eligibility = useSelector((state) => state.paper.eligibility);
  if (completed) {
    const item = localStorage.getItem('answerArray')
    if(item){
      dispatch(answerActions.setInitial(JSON.parse(item)))
      dispatch(qChangerActions.setQ(JSON.parse(item).length - 1))
    }else{
      localStorage.setItem('answerArray', JSON.stringify([]))
    }
    return <Navigate to={`/${examid}/${personId}/exam`} />;
  }
  return (
    <Fragment>
      {code && <WaitingCounter compFunc={setCompleted} />}
      {!code && (
        <div className="h-screen w-screen flex flex-col items-center justify-center">
          <div className="text-2xl w-1/3 border border-[#3c485245] px-1 py-2 rounded-xl mb-5 flex flex-col overflow-hidden">
            <label>Exam Code</label>
            <input
              className="text-[1.5rem] m-2 mt-3"
              type="text"
              ref={examCodeRef}
              placeholder={"Type Exam Code Here"}
              required
            />
          </div>
          <Button onClick={getPaper}>Submit</Button>
          {!eligibility && <div className="text-xl font-medium font-chimono text-red-600 mt-3">You Already apperead the test</div>}
        </div>
      )}
    </Fragment>
  );
};

export default Waiting;
