import React, { Fragment, useRef, useState } from "react";
// import IEMLogo from '../../Images/IEMLogo-removebg-preview.png'
import Button from "../UI/Button";
import { Navigate, useNavigate, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { authActions } from "../../Store";

const StudentLogin = (props) => {
  const personType = useSelector((state) => state.auth.type);
  const { type } = useParams();
  const person = personType !== '' ? personType : type;
  const dispatch = useDispatch();
  const emailRef = useRef();
  const numberRef = useRef();
  const enrollmentRef = useRef();
  const teacherIdRef = useRef();
  const nameRef = useRef();
  const [value, setValue] = useState();
  const [signUp, setSignUp] = useState(false);
  const fetchData = async () => {
    let urllink = "";
    let bodyJson = "";
    if (signUp) {
      if (person === "student") {
        urllink = "/signup";
        bodyJson = JSON.stringify({
          enrollment_number: enrollmentRef.current.value,
          name: nameRef.current.value,
          email: emailRef.current.value,
          phone_number: numberRef.current.value,
        });
      } else {
        urllink = '/teachersignup'
        bodyJson = JSON.stringify({
          teacherid: teacherIdRef.current.value,
          name: nameRef.current.value,
          email: emailRef.current.value,
          phoneNumber: numberRef.current.value,
        })
      }
    } else {
      if (person === "student") {
        urllink = '/login'
        bodyJson = JSON.stringify({
          enrollment_number: enrollmentRef.current.value,
          phone_number: numberRef.current.value,
        })
      } else {
        urllink = '/teacherlogin'
        bodyJson = JSON.stringify({
          teacherid: teacherIdRef.current.value,
          phoneNumber: numberRef.current.value,
        })
      }
    }
    const Response = await fetch(urllink, {
      method: "POST",
      body: bodyJson,
      headers: { "Content-Type": "application/json" },
    });
    if (Response.ok) {
      const data = await Response.json();
      if(data.auth){
        dispatch(authActions.logIn())
        sessionStorage.setItem('auth', JSON.stringify(true))
        if(person === 'student'){
          dispatch(authActions.setId(enrollmentRef.current.value))
          sessionStorage.setItem('id', JSON.stringify(enrollmentRef.current.value))
        }else{
          dispatch(authActions.setId(teacherIdRef.current.value))
          sessionStorage.setItem('id', JSON.stringify(teacherIdRef.current.value))
        }
      }
      
    }
  };
  const onsubmitHandler = (e) => {
    e.preventDefault();
    fetchData();
  };
  const isAuth = useSelector((state) => state.auth.isAuthenticated);
  if (isAuth && person==='student') {
    return <Navigate to={`/student`} />;
  }
  if (isAuth && person==='teacher') {
    return <Navigate to={`/teacher`} />;
  }
  const signupChangeHandler = (e) => {
    e.stopPropagation();
    setSignUp((state) => !state);
  };

  return (
    <div className="w-full h-full">
      <div className="font-bold text-3xl font-poppins text-[#3c4852]">
        {!signUp ? "Login" : "SignUp"}
      </div>
      <div className="mt-2 text-sm">
        or&nbsp;
        <span
          className="text-[#08BD80] pb-1 border-[#08BD80] border-dashed border-b cursor-pointer"
          onClick={signupChangeHandler}
        >
          {!signUp ? "Create your account" : "login to your account"}
        </span>
      </div>
      <form className="mt-8">
        {signUp && (
          <Fragment>
            <div className="text-2xl w-4/5 border-[#3c485245] border px-2 py-3 rounded-xl mb-5">
              <input
                className="h-[40px] placeholder:text-[#828384] border-none outline-none bg-transparent w-4/5 "
                placeholder="Name"
                ref={nameRef}
              />
            </div>
            <div className="text-2xl w-4/5 border-[#3c485245] border px-2 py-3 rounded-xl mb-5">
              <input
                className="h-[40px] placeholder:text-[#828384] border-none outline-none bg-transparent w-4/5 "
                person="text"
                placeholder="Email"
                ref={emailRef}
              />
            </div>
          </Fragment>
        )}

        <div className="text-2xl w-4/5 border-[#3c485245] border px-2 py-3 rounded-xl mb-5">
          <input
            className="h-[40px] placeholder:text-[#828384] border-none outline-none bg-transparent w-4/5 "
            person="text"
            placeholder={
              person === "student" ? "Enrollment Number" : "Teacher ID"
            }
            ref={person === "student" ? enrollmentRef : teacherIdRef}
          />
        </div>
        <div className="text-2xl w-4/5 border-[#3c485245] border px-2 py-3 rounded-xl mb-5">
          <input
            className="h-[40px] placeholder:text-[#828384] border-none outline-none bg-transparent w-4/5 "
            person="text"
            placeholder="Phone Number"
            ref={numberRef}
          />
        </div>
        <Button
          style={{
            width: "auto",
            fontSize: "21px",
            position: "relative",
            marginTop: "12px",
          }}
          onClick={onsubmitHandler}
        >
          {signUp ? "SignUp" : "LogIn"}
        </Button>
      </form>
    </div>
  );
};

export default StudentLogin;
