import React from 'react'
import Options from './Options'

const Questions = (props) => {
  return (
    <div className='bg-[#F1F3F6] rounded-2xl w-full h-auto p-5 mb-5'>
        <div className='font-normal text-3xl mb-3'>Question {props.Qnum}</div>
        <div className='bg-white rounded-2xl text-2xl p-4  text-[#7F8086]'>{props.Qtext}</div>
        <div className=''>
            {props.options.map((option,index)=><Options Onum={index+1} Otext={option.option} correctAnswer={props.correctAnswer}/>)}
        </div>
    </div>
  )
}

export default Questions