import React, { Fragment } from "react";
import Button from "../../UI/Button";
import QuestionMark from "./QuestionMark";
import { faCircle, faXmark } from "@fortawesome/free-solid-svg-icons";
import { faCircle as hollow } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useSelector, useDispatch } from "react-redux";
import { motion } from "framer-motion";
import { modalActions, paperActions } from "../../../Store";
import { useState } from "react";

const Progress = (props) => {
  const marked = useSelector((state) => state.answer.marked);
  const answered = useSelector((state) => state.answer.answerArray);
  return (
    <div className='justify-around flex items-center w-full text-xl'>
      <span className="mx-2 my-2">
        <FontAwesomeIcon
          style={{ color: "#08bd80", marginRight: "10px" }}
          icon={faCircle}
        />
        {`${answered.filter((i) => i != null).length} answered`}
      </span>
      <span className="mx-2 my-2">
        <FontAwesomeIcon
          style={{ color: "#ffaf41", marginRight: "10px" }}
          icon={faCircle}
        />
        {`${marked.length} marked`}
      </span>
      <span className="mx-2 my-2">
        <FontAwesomeIcon
          style={{ color: "#b2bcc2", marginRight: "10px" }}
          icon={hollow}
        />
        {`${props.questionList.length - answered.filter((i) => i != null).length} Unanswered`}
      </span>
    </div>
  );
};

const EModal = (props) => {
  const dispatch = useDispatch();
  const open = useSelector((state) => state.modal.examopen);
  const closeModal = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(modalActions.examclose());
  };
  const submitHandler = (e) =>{
    e.preventDefault();
    e.stopPropagation();
    dispatch(paperActions.endExam())
  }

  return (
    <div className='h-screen w-screen bg-[#00000060] flex items-center justify-center z-20 absolute top-0 left-0' onClick={closeModal}>
      <motion.div
        initial={{ x: open ? "35vw" : "0vw" }}
        onClick={(e) => e.stopPropagation()}
        animate={{ x: open ? "0vw" : "35vw" }}
        transition={{ duration: 0.7 }}
        className=" bg-white pl-4 z-40 rounded-tl-md rounded-bl-md py-2"
        id="mainModal"
      >
        <div className='text-[2rem] mt-4 mb-3 text-[#3c4852]'>
          <FontAwesomeIcon icon={faXmark} onClick={closeModal} />
        </div>
        <div className="flex flex-col items-center justify-center">
          <Progress questionList={props.questionList} />
          <Button onClick={submitHandler} style={{ padding: "5px 10px", fontSize: "25px" }}>
            Comfirm Submit
          </Button>
        </div>
      </motion.div>
    </div>
  );
};

const ExamControl = (props) => {
  const dispatch = useDispatch();
  const open = useSelector((state) => state.modal.examopen);
  const marked = useSelector((state) => state.answer.marked);
  const answered = useSelector((state) => state.answer.answerArray);
  const openModal = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(modalActions.examopen());
  };
  return (
    <Fragment>
      <div className='bg-white w-4/12 relative flex flex-col items-center justify-center shadow-2xl'>
        <Button
          onClick={openModal}
          style={{
            position: "absolute",
            padding: "6px 12p,",
            fontSize: "21px",
            top: "20px",
            right: "20px",
          }}
        >
          End Test
        </Button>
        <Progress questionList={props.questionList} />
        <div className='bg-white w-full rounded-lg px-[20px] flex flex-wrap justify-start h-2/5 overflow-y-scroll'>
          {props.questionList.map((i, index) => (
            <QuestionMark
              id={index}
              answered={answered[index]}
              marked={marked[index]}
              num={index + 1}
            />
          ))}
        </div>
      </div>
      {open && <EModal questionList={props.questionList}></EModal>}
    </Fragment>
  );
};

export default ExamControl;
