const QModal = (props) => {
  const dispatch = useDispatch();
  const open = useSelector((state) => state.modal.questionopen);
  const closeModal = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(modalActions.questionclose());
  };
  return (
    <div className='w-screen h-screen bg-[#00000050] flex justify-center items-center z-20 absolute top-0 left-0' onClick={closeModal}>
      <motion.div
        initial={{ x: open ? "35vw" : "0vw" }}
        onClick={(e) => e.stopPropagation()}
        animate={{ x: open ? "0vw" : "35vw" }}
        transition={{ duration: 0.7 }}
        className='w-[35vw] h-[60vh] bg-[#ffffff] pl-5 z-40  overflow-y-scroll rounded-xl'
        id="mainModal"
      >
        <div className='sticky text-[2rem] mt-5 mb-3 text-[#3c4852]'>
          <FontAwesomeIcon icon={faXmark} onClick={closeModal} />
        </div>
        {props.children}
      </motion.div>
    </div>
  );
};

const AddQuestion = () => {
  const dispatch = useDispatch();
  const open = useSelector((state) => state.modal.questionopen);
  const [navigate, setNavigate] = useState(false)
  const openQmodal = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(modalActions.questionopen());
  };
  const QuestionList = useSelector((state) => state.question.QuestionArray);
  const submitQuestion = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(
      examActions.addQuestionPaper({
        QuestionData: QuestionList,
      })
    );
    sendQuestionData();
  };
  const detail = useSelector((state) => state.exampaper);
  const sendQuestionData = async() =>{
    const Response = await fetch('/addQ',{
        method: 'POST',
        body: JSON.stringify({
            'ExamPaper': {
                examid: detail.id,
                questionList: QuestionList
            }
        }),
        headers: { "Content-Type": "application/json" },
    })
    if(Response.ok){
        setNavigate(true)
    }
  }

  if(navigate){
    return <Navigate to='/sharecode' />
  }
  return (
    <Fragment>
      <div className="w-screen overflow-hidden">
        <Button
          style={{
            "align-items": "center",
            "justify-content": "center",
            transform: "none",
            position: "absolute",
            width: "141px",
            height: "48px",
            right: "60px",
            top: "37px",
            "font-size": "24px",
            display: "flex",
            "line-height": "40px",
          }}
          onClick={submitQuestion}
        >
          Submit
        </Button>
        <div className='relative m-auto mt-32 w-[70%] overflow-y-scroll' style={{height: 'calc(100vh - 145px)'}}>
          {QuestionList.map((question, index) => (
            <Questions
              key={index + 1}
              Qnum={index + 1}
              Qtext={question.Question}
              options={question.OptionList}
              correctAnswer={question.CorrectAnswer}
            />
          ))}
          <button className='font-bold text-2xl p-3 border-none text-[#4C84EA] bg-[#EAF0FD] rounded-md w-full' onClick={openQmodal}>
            Add Question
          </button>
        </div>
      </div>
      {open && (
        <QModal>
          <QuestionForm />
        </QModal>
      )}
    </Fragment>
  );
};

export default AddQuestion;
