import React, { Fragment, useEffect } from 'react'
import Button from '../UI/Button'
import { useDispatch, useSelector } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faXmark } from '@fortawesome/free-solid-svg-icons';
import { modalActions } from '../../Store';
import { motion } from 'framer-motion';
import TestForm from './TestForm';

const TModal = (props) => {
    const dispatch = useDispatch();
    const open = useSelector(state => state.modal.testopen)
    const closeModal = (e) => {
        e.preventDefault();
        e.stopPropagation();
        dispatch(modalActions.testclose());
    }
    return (
        <div className='h-screen w-screen bg-[#00000050] flex justify-center items-center z-20 absolute top-0 left-0' onClick={closeModal}>
            <motion.div
                initial={{ x: open ? "35vw" : "0vw" }}
                onClick={(e) => e.stopPropagation()}
                animate={{ x: open ? "0vw" : "35vw" }}
                transition={{ duration: 0.7 }}
                className='w-[35vw] h-[60vh] bg-white pl-5 z-40 overflow-y-scroll rounded-xl'
                id="mainModal"
            >
                <div className='sticky text-3xl mt-5 mb-3 text-[#3c4852]'>
                    <FontAwesomeIcon icon={faXmark} onClick={closeModal} />
                </div>
                {props.children}
            </motion.div>
        </div>
    );
};

const TeacherMain = () => {
    const dispatch = useDispatch();
    const open = useSelector(state=> state.modal.testopen);
    const openModal = () => {
        dispatch(modalActions.testopen())
    }
  return (
    <div className='w-screen h-screen flex items-center justify-center'>
        <Button style={{'padding': '9px 15px','fontSize': '23px'}} onClick={openModal}>Create Test</Button>
        {open  && <TModal><TestForm/></TModal>}
    </div>
  )
}

export default TeacherMain