import { createSlice } from "@reduxjs/toolkit";
const initial = {QuestionArray: []}

export const questionSlice = createSlice({
    name: 'Question',
    initialState: initial,
    reducers:{
        addquestion(state,action){
            state.QuestionArray.push({
                Question: action.payload.Question,
                OptionList: action.payload.OptionList,
                CorrectAnswer: action.payload.CorrectAnswer,
                CorrectMarks: action.payload.CorrectMarks,
                NegativeMarks: action.payload.NegativeMarks
            })
        },
        deletequestion(state,action){
            state.QuestionArray.slice(action.payload,1)
        },
        editquestion(state,action){
            state.QuestionArray[action.payload.index].Question = action.payload.question
        },
        editoption(state,action){
            state.QuestionArray[action.payload.Qindex].optionList.slice(action.payload.Oindex,1,action.payload.option)
        },
        deleteoption(state,action){
            state.QuestionArray[action.payload.Qindex].optionList.slice(action.payload,1)
        },
    }
})