import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBookmark } from "@fortawesome/free-solid-svg-icons";
import { useDispatch } from "react-redux";
import { qChangerActions } from "../../../Store/index";

const QuestionMark = (props) => {
  const dispatch = useDispatch();
  return (
    <div
      onClick={() => dispatch(qChangerActions.setQ(props.id))}
      className='p-5 m-3 text-2xl h-7 w-7 border-2 border-[#3c485291] text-[#404c56] flex justify-center items-center rounded-lg relative'
      style={{
        color: props.answered ? "#12bf86" : "#404c56",
        backgroundColor: props.answered ? "#e6f8f2" : "",
      }}
    >
      {props.num}
      {props.marked && (
        <FontAwesomeIcon
          style={{
            color: "#ffad3b",
            position: "absolute",
            bottom: "-10px",
            right: "-7px",
            fontSize: "1.1rem",
          }}
          icon={faBookmark}
        />
      )}
    </div>
  );
};

export default QuestionMark;
