import React from "react";
// import Quiz from "../components/StudentPage/Quiz";
import { Routes, Route } from "react-router-dom";
import PageNotFound from "./PageNotFound";
import { useSelector } from "react-redux";
import AddQuestion from "../components/TeacherPage/AddQuestion";
import TeacherMain from "../components/TeacherPage/TeacherMain";
import Examination from "../components/StudentPage/Exam/Examination";
import Results from "../components/StudentPage/Results/Results";
import Home from "../components/OpeningPage/Home";
import StudentOpeningPage from "../components/OpeningPage/StudentOpeningPage";
import Waiting from "../components/StudentPage/Waiting";
import ShareCode from "../components/TeacherPage/ShareCode";

function MainNavigation() {
  const isAuth = useSelector((state) => state.auth.isAuthenticated);
  const auth = sessionStorage.getItem('auth')
  const authenticated = isAuth || auth 
  window.onclose = function(){
    sessionStorage.removeItem('auth');
    sessionStorage.removeItem('id');
  }
  return (
    <main className='overflow-hidden h-screen w-screen flex flex-col items-center'>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/:type/login" element={<StudentOpeningPage />} />
        {authenticated && <Route path="/teacher" element={<TeacherMain />} />}
        {authenticated && <Route path="/student" element={<Waiting />} />}
        {authenticated && <Route path="/:examid/:enrollment/exam" element={<Examination />} />}
        {authenticated && <Route path="/marks/:enrollment" element={<Results />} />}
        {authenticated && <Route
          path="/teacher/:examid/addQuestion"
          element={<AddQuestion />}
        />}
        {authenticated && <Route path="/sharecode" element={<ShareCode/>}/>}
        <Route path="*" element={<PageNotFound />} />
      </Routes>
    </main>
  );
}
export default MainNavigation;
