import React, { useState } from "react";
import ExamControl from "./ExamControl";
import ExamHeader from "./ExamHeader";
import QuestionCarausel from "./QuestionCarausel";
import Button from "../../UI/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBookmark } from "@fortawesome/free-solid-svg-icons";
import { useDispatch } from "react-redux";
import { qChangerActions, answerActions } from "../../../Store";
import { useSelector } from "react-redux";
import { Navigate } from "react-router-dom";

const Examination = () => {
  const dispatch = useDispatch();
  const currentQ = useSelector((state) => state.qChanger.currentQNo);
  const questionList = useSelector((state) => state.paper.paper);
  const [prevdisable, setPrevDiable] = useState(true);
  const [nextdisable, setNextDiable] = useState(false);
  const prevHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (currentQ <= 0) {
      setPrevDiable(true);
      setNextDiable(false);
    } else {
      setPrevDiable(false);
      setNextDiable(false);
      dispatch(qChangerActions.prevQ());
    }
  };
  const saveNextHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();
    // dispatch(answerActions.add({index: currentQ, answer: answer}))
    if (currentQ >= (questionList.length - 1)) {
      setNextDiable(true);
      setPrevDiable(false);
    } else {
      setNextDiable(false);
      setPrevDiable(false);
      dispatch(qChangerActions.nextQ());
    }
  };

  const markedHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();
    dispatch(answerActions.add({index: currentQ, answer: null}))
    dispatch(answerActions.mark(currentQ));
    if (currentQ >= (questionList.length - 1)) {
      setNextDiable(true);
      setPrevDiable(false);
    } else {
      setNextDiable(false);
      setPrevDiable(false);
      dispatch(qChangerActions.nextQ());
    }
  };
  const examEnded = useSelector(state => state.paper.examEnded);
  const person = useSelector(state => state.auth.id);
  const id = sessionStorage.getItem('id')
  const personId = person || Number(JSON.parse(id));
  if(examEnded){
    return <Navigate to={`/marks/${personId}`} />
  }
  return (
    <div className='overflow-hidden h-screen w-screen flex bg-[#fbfbfc]'>
      <div className='relative w-full'>
        <ExamHeader />
        <QuestionCarausel questionList={questionList} />
        <div className='absolute w-full bottom-0 left-0 h-1/6 bg-transparent flex items-center justify-between px-5 py-1'>
          <Button
            style={{
              "background-color": "#FFFFFF",
              color: "#404c56",
              border: "#404c56a6 solid 2px",
              padding: "9px 15px",
              fontSize: "22px",
            }}
            disabled={prevdisable}
            onClick={prevHandler}
          >
            Previous
          </Button>
          <span style={{ display: "flex" }}>
            <Button
              style={{
                "background-color": "#FFFFFF",
                color: "#404c56",
                border: "#404c56a6 solid 2px",
                display: "flex",
                marginRight: "10px",
                padding: "9px 15px",
                fontSize: "22px",
              }}
              onClick={markedHandler}
            >
              <FontAwesomeIcon
                style={{
                  color: "#ffad3b",
                  fontSize: "1.5rem",
                  marginRight: "4px",
                }}
                icon={faBookmark}
              />{" "}
              Marked
            </Button>
            <Button
              onClick={saveNextHandler}
              disabled={nextdisable}
              style={{ padding: "9px 15px", fontSize: "22px" }}
            >
              {`Save & Next`}
            </Button>
          </span>
        </div>
      </div>
      <ExamControl questionList={questionList}/>
    </div>
  );
};

export default Examination;
