import React, { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";
import Button from "../UI/Button";
import { examActions } from "../../Store";

const TestForm = () => {
  const dispatch = useDispatch();
  const examNameRef = useRef();
  const SubcodeRef = useRef();
  const sessionRef = useRef();
  const semesterRef = useRef();
  const dateRef = useRef();
  const startTimeRef = useRef();
  const durationRef = useRef();
  const [testform, setTestform] = useState(false);
  const testCreate = (e) => {
    e.preventDefault();
    e.stopPropagation();
    createTest();
  };
  const createTest = async () => {
    const Response = await fetch("/createTest", {
      method: "POST",
      body: JSON.stringify({
        ExamName: examNameRef.current.value,
        SubjectCode: SubcodeRef.current.value,
        Session: sessionRef.current.value,
        Date: dateRef.current.value,
        semester: semesterRef.current.value,
        StartTime: startTimeRef.current.value + ":00",
        duration: durationRef.current.value,
      }),
      headers: { "Content-Type": "application/json" },
    });
    if (Response.ok) {
        const data = await Response.json();
        const examid = data.examid;
        dispatch(examActions.addExamId(examid));
        setTestform(true);
    }
  };
  const details = useSelector((state) => state.exampaper);
  if(testform){
    return <Navigate to={`/teacher/${details.id}/addQuestion`} />;
  }

  return (
    <div className=''>
      <form className='w-full mb-5'>
        <div className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
          <label>Exam Name</label>
          <input
          className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
            type="text"
            ref={examNameRef}
            placeholder={"Ex:- Midsem1 Digital Electronics"}
            required
          />
        </div>
        <div className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
          <label>Subject Code</label>
          <input
          className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
            type="text"
            ref={SubcodeRef}
            placeholder={"Type Subject Code Here"}
            required
          />
        </div>
        <div className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
          <label>Session</label>
          <select className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none" ref={sessionRef} required>
            <option className="p-1">2022-2023</option>
          </select>
        </div>
        <div className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
          <label>Semester</label>
          <input
          className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
            type="number"
            min={1}
            ref={semesterRef}
            max={8}
            placeholder={"Ex- 1"}
            required
          />
        </div>
        <div>
          <span className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
            <label>Exam Date</label>
            <input
            className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
              type="date"
              placeholder="dd-mm-yyyy"
              ref={dateRef}
              required
            />
          </span>
          <span className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
            <label>Start Time</label>
            <input
            className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
              type="time"
              defaultValue={"00:00"}
              ref={startTimeRef}
              required
            />
          </span>
          <span className="text-2xl w-full border border-[#3c485245] px-1 py-2 m-auto rounded-xl mb-5 flex flex-col overflow-hidden">
            <label>Duration (in min)</label>
            <input
            className="h-[40px] border-none text-lg bg-transparent w-full mt-3 placeholder:text-[#828384] focus:outline-none"
              type="number"
              placeholder="Ex:- 180"
              ref={durationRef}
              required
            />
          </span>
        </div>
        <Button
          style={{
            padding: "9px 16px",
            fontSize: "23px",
          }}
          onClick={testCreate}
        >
          Create Question Paper
        </Button>
      </form>
    </div>
  );
};

export default TestForm;
